
// $card-cap-bg: rgbacus($black, .1)
// $font-family-sans-serif: SpaceMonkey
$my-blue: #6792bd
$my-green: #6cc3af
address.vcard
  font-style: normal
.vcard 
  .fn
    font-size: 2em
  
  .adr
    .street-address
      display: block
    .postal-code
      margin-left: 1em
  .bio-label::after
      content: ": "
    
body
  font-family: "Avenir Next", "Helvetica Neue", Helvetica, Arial, sans-serif
  font-size: 14px
  line-height: 1.42857143
  // color: #333
  // background-color: $my-blue
  background-image: linear-gradient(180deg, $my-green, $my-blue)
  padding-bottom: 1rem

.navbar-brand
  font-size: 3rem
  margin-left: 2rem

.tab-content
  background-color: $white
  padding-bottom: 1rem
  padding-top: 4rem
  top: -3rem
  position: relative

img.avatar
  border-radius: 4px
  float: left
  margin-right: 30px
  height: 10em
  
// .social-media-icons
//   float: right

.intro
  font-size: 1.3em
  text-align: center
  background-color: $my-blue
  background-image: linear-gradient(180deg, rgba(255, 255, 255, .7), rgba(255, 255, 255, .2))
  border-radius: 1em
  margin: 2em
  padding: 1em 0 .8em

a.svg
  position: relative
  display: inline-block
  color: $black

  &:after
    position: absolute 
    top: 0 
    right: 0 
    bottom: 0 
    left: 0


@-webkit-keyframes spinner 
  from 
    -webkit-transform: rotateY(0deg)
  to
    -webkit-transform: rotateY(-360deg)


@keyframes spinner 
  from 
    -moz-transform: rotateY(0deg)
    -ms-transform: rotateY(0deg)
    transform: rotateY(0deg)

  to 
    -moz-transform: rotateY(-360deg)
    -ms-transform: rotateY(-360deg)
    transform: rotateY(-360deg)
    
svg
  margin: .5em
    
svg.run-animation
  -webkit-animation-name: spinner
  -webkit-animation-timing-function: linear
  -webkit-animation-iteration-count: infinite
  -webkit-animation-duration: 3s

  animation-name: spinner
  animation-timing-function: linear
  animation-iteration-count: infinite
  animation-duration: 3s

  -webkit-transform-style: preserve-3d
  -moz-transform-style: preserve-3d
  -ms-transform-style: preserve-3d
  transform-style: preserve-3d
   
  -webkit-animation-play-state: paused
  animation-play-state: paused

  &:hover
    -webkit-animation-play-state: running
    animation-play-state: running
  
li.experience.vevent.position
  list-style: none
  display: block
  margin: 1em
  
  span
    display: block
    
    &.jobtitle
      font-weight: bold
      
div.iconcredit
  font-size: .5em
  margin: 2em
    
// .nav-tabs
//   border-bottom: 0
//   li.active > a:hover, li > a:hover
//     border-bottom: 0

// a.nav-link
//   color: $black

// a.nav-link.active
//   font-weight: bold
    
.quote p
  > *
    text-indent: 2em
    margin-right: 1em
    position: absolute
  &::after, &::before
    // color: gray
    font-size: 4em
    font-family: sans-serif
    position: absolute
  &::before
    content: ""
    top: -.5em
    left: -.5em
  &::after
    content: ""
    right: .5em

.github-view-source-link
  font-size: .9em


.tab-content
  padding: 4em 1em
  border-radius: .2em

.tab-pane
  padding-left: 1em

.quote-container
  position: relative
  font-size: 1.2em
  
.card
  @include gradient-y(tint-color($gray-500, 50%), $white)
  margin-bottom: 2em

.navbar
  // @include gradient-y(rgba(255, 255, 255, 1), rgba(255, 255, 255, .5), 80%)
  background-color: $white
  border-bottom: solid 5px $gray-400
  // padding-bottom: 1rem

.navbar-bottom-line
  @include gradient-x(tint-color($gray-500, 50%), $white)

li
  .rounded-pill
    display: none
    margin-left: 1em
  &:hover 
    .rounded-pill
      display: inline

.card-body.pill-hover
  .rounded-pill
    display: none
    margin-left: 1em
  &:hover 
    .rounded-pill
      display: inline

.project
  padding-left: 1em
  strong
    margin: 1em 0 1em
    display: inline-block
dl
  padding-left: 1em
dt
  font-weight: 600

.employer-details
  padding-left: 1em
  border-left: solid 1px black

  
.award, .awards, .awards > p, .awards > .award > p
  padding-left: 1em

.novice:hover::after
  @extend .badge
  @extend .rounded-pill
  @extend .bg-success
  margin-left: 1em
  content: "Novice"

.good:hover::after
  @extend .badge
  @extend .rounded-pill
  @extend .bg-warning
  @extend .text-dark
  margin-left: 1em
  content: "Good"

.pro:hover::after
  @extend .badge
  @extend .rounded-pill
  @extend .bg-danger
  margin-left: 1em
  content: "Pro"
